#iframewrapper {
    float: right;
    width: 21%;
    position: relative;
}

#RedColor {
    color: red;
    font-family: Roboto;
    font-style: normal;
}

button {
    /* Submit */
    position: static;
    width: 60px;
    height: 30px;
    left: 20px;
    top: 10px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    /* identical to box height */
    text-align: center;
    background-color: black;
    /* White */
    color: #FFFFFF;
    /* Inside Auto Layout */
    flex: none;
    order: 0;
    align-self: center;
    margin: 10px 0px;
}


.centered {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.CenterLine1 {
    width: 564px;
    height: 172px;
    left: 302px;
    top: 262px;
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 80px;
    line-height: 100.8%;
    /* or 81px */
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.CenterLine2 {
    width: 457px;
    height: 37px;
    left: 302px;
    top: 436px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 26px;
    line-height: 125%;
    /* or 32px */
    color: #FFFFFF;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.image {
    opacity: 0.5;
}

